/* --- Modules --- */
html,
body {
  width: 100%;
  height: 100%; }

html {
  background: #fff;
  overflow: hidden; }

body {
  overflow: auto; }

.ph14-wrap {
  max-width: 40rem;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem .5rem; }

.ph14-section {
  padding: 1rem 0 .5rem; }
  .ph14-section > h1:first-child,
  .ph14-section > h2:first-child,
  .ph14-section > h3:first-child,
  .ph14-section > h4:first-child,
  .ph14-section > h5:first-child,
  .ph14-section > h6:first-child {
    margin-top: 0; }

body {
  color: #555;
  font-family: "sans-serif";
  font-size: 16px;
  line-height: 1.4; }

h1, h2, h3, h4, h5, h6 {
  color: #222; }

p {
  font-size: 1rem; }

table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 1rem; }

th,
td {
  padding: .3rem; }

th {
  text-align: left;
  color: #222; }

thead tr + tr th {
  border-top: 1px solid #ddd; }

tbody tr:nth-child(odd) td {
  background: #f8f8f8; }

tfoot {
  color: #888;
  font-size: .95rem;
  font-style: italic; }
  tfoot td {
    border-top: 1px dashed #ddd; }

form {
  margin-bottom: 1rem; }

fieldset {
  padding: 1rem;
  margin: 0 0 .5rem;
  border: 1px dashed #ddd; }

label {
  display: inline-block;
  margin-right: .5rem;
  margin-bottom: .2rem;
  font-weight: 600;
  color: #222; }

input[type=color],
input[type=date],
input[type=datetime-local],
input[type=email],
input[type=month],
input[type=number],
input[type=password],
input[type=search],
input[type=tel],
input[type=text],
input[type=time],
input[type=url],
input[type=week],
textarea {
  border: 2px solid #ddd;
  background: #fff;
  padding: .5rem .7rem;
  box-shadow: none;
  line-height: 1.4;
  outline: none !important;
  transition: border-color 0.1s linear; }
  input[type=color]:hover,
  input[type=date]:hover,
  input[type=datetime-local]:hover,
  input[type=email]:hover,
  input[type=month]:hover,
  input[type=number]:hover,
  input[type=password]:hover,
  input[type=search]:hover,
  input[type=tel]:hover,
  input[type=text]:hover,
  input[type=time]:hover,
  input[type=url]:hover,
  input[type=week]:hover,
  textarea:hover {
    border-color: #bebebe; }
  input[type=color]:focus,
  input[type=date]:focus,
  input[type=datetime-local]:focus,
  input[type=email]:focus,
  input[type=month]:focus,
  input[type=number]:focus,
  input[type=password]:focus,
  input[type=search]:focus,
  input[type=tel]:focus,
  input[type=text]:focus,
  input[type=time]:focus,
  input[type=url]:focus,
  input[type=week]:focus,
  textarea:focus {
    border-color: #2890d2; }
